<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top">
				<div class="search_box">
					<el-input v-model="fund_name" prefix-icon="el-icon-search" placeholder="子基金全称" clearable>
					</el-input>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-input v-model="project_name" placeholder="项目名称" clearable>
					</el-input>
				</div>
				<div class="btn_box">
					<button @click="getInitData(1)">搜索</button>
				</div>
			</div>
		</div>
		<div class="table_title flex_between">
			<div class="">
				子基金投资组合总数<span>(&nbsp;{{ total }}&nbsp;)</span>
			</div>
			<div class="flex_end">
				<button class="hover_btn flex_center" @click="downloadTemplet">
					<span class="iconfont icon-daoru"></span>
					下载子基金投资组合模板
				</button>
				<div class="">
					<label for="upload-fund-input">
						<div class="table-group-title-btn btn-blue-border">
							<span class="iconfont icon-daochu"></span>导入子基金投资组合信息
						</div>
					</label>
					<input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadInfo($event)" />
				</div>
				<button class="blue_btn flex_center" @click="downloadInfo">
					<span class="iconfont icon-daoru"></span>
					导出excel表格
				</button>
			</div>
		</div>
		<div class="table">
			<el-table :data="tableData" border style="width: 100%">
				<el-table-column sortable prop="id" label="序号" width="100">
					<template slot-scope="scope">
						{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="fund_id" label="ID" width="100">
				</el-table-column>
				<el-table-column sortable prop="name" label="子基金全称" width="200">
				</el-table-column>
				<el-table-column sortable prop="short_name" label="子基金简称" width="200">
				</el-table-column>
				<el-table-column sortable prop="number" label="编号" width="100">
				</el-table-column>
				<el-table-column sortable prop="company" label="企业名称" width="220">
				</el-table-column>
				<el-table-column sortable prop="project" label="项目名称" width="180">
				</el-table-column>
				<el-table-column sortable prop="first_invest_date" label="首次投资日期" width="180">
				</el-table-column>
				<el-table-column sortable prop="unit" label="币种" width="120">
					<template slot-scope="scope">
						{{ scope.row.unit == 1?'人民币':'美元' }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="invest_money" label="投资额（万元）" width="150">
				</el-table-column>
				<el-table-column sortable prop="invest_evaluate" label="投资时估值(亿元)" width="170">
				</el-table-column>
				<el-table-column sortable prop="invest_scale" label="投资占比" width="150">
					<template slot-scope="scope">
						{{ scope.row.invest_scale }}&nbsp;%
					</template>
				</el-table-column>
				<el-table-column sortable prop="fair_value" label="公允价值(万元)" width="150">
				</el-table-column>
				<el-table-column sortable prop="update_time" label="更新日期" width="180">
				</el-table-column>
				<el-table-column sortable prop="back" label="回款返本（万元）" width="180">
				</el-table-column>
				<el-table-column sortable prop="income" label="回款收益-税后（万元）" width="200">
				</el-table-column>
				<el-table-column sortable prop="back_scale" label="返本比例" width="150">
					<template slot-scope="scope">
						{{ scope.row.back_scale }}&nbsp;%
					</template>
				</el-table-column>
				<template slot="empty">
					<div class="table-nodata flex_start">
						<img src="../../images/table_nodata.png">
						<span>没有对应数据</span> 
					</div>
				</template>
			</el-table>
			<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</div>
		
		<!--  导入信息差异弹窗开始-->
		<el-dialog title="导入信息与系统信息差异对照" :visible.sync="is_dialog_show" width="1030px" center :append-to-body='true'>
			<div class="dialog_body">
				<div class="difference">
					<div class="difference_title flex_between">
						<div class="difference_title_left">
							<!-- 1、导入表中共有{{ fund_not_exits.length }}支子基金不存在于系统中 -->
							1、系统中不存在的基金，共：{{fund_not_exits.length}}条
						</div>
						<div class="cursor" @click="changeDifferenceStatus(1)">
							<span v-if="!currentIndex1" class="iconfont icon-webicon212"></span>
							<span v-else class="iconfont icon-xiangshang"></span>
						</div>
					</div>
					<div class="difference_main table" v-if="currentIndex1&&fund_not_exits.length">
						<el-table :data="fund_not_exits" stripe style="width: 100%" max-height="400"> 
							<el-table-column prop="fund_id" label="序号" width="100">
							</el-table-column>
							<el-table-column prop="child_fund_name" label="子基金全称">
							</el-table-column>
							<el-table-column prop="fund_short_name" label="子基金简称" width="250">
							</el-table-column>
  						</el-table>
					</div>
				</div>
				<div class="difference">
					<div class="difference_title flex_between">
						<div class="difference_title_left">
							<!-- 2、导入表中共有{{ project_not_exits.length }}个企业名称不存在于系统中 -->
							2、系统中不存在的企业，共：{{project_not_exits.length}}条
						</div>
						<div class="cursor" @click="changeDifferenceStatus(2)">
							<span v-if="!currentIndex2" class="iconfont icon-webicon212"></span>
							<span v-else class="iconfont icon-xiangshang"></span>
						</div>
					</div>
					<div class="difference_main table" v-if="currentIndex2&&project_not_exits.length">
						<el-table :data="project_not_exits" stripe style="width: 100%" max-height="400"> 
							<el-table-column prop="fund_id" label="序号" width="100">
    							</el-table-column>
    							<el-table-column prop="company" label="企业名称">
    							</el-table-column>
  						</el-table>
					</div>
				</div>
				<div class="difference">
					<div class="difference_title flex_between">
						<div class="difference_title_left">
							<!-- 3、系统中有{{ miss.length }}个导入表中不存在的企业名称 -->
							3、导入表不存在的企业名称，共：{{miss.length}}条
						</div>
						<div class="cursor" @click="changeDifferenceStatus(3)">
							<span v-if="!currentIndex3" class="iconfont icon-webicon212"></span>
							<span v-else class="iconfont icon-xiangshang"></span>
						</div>
					</div>
					<div class="difference_main table" v-if="currentIndex3&&miss.length">
						<el-table :data="miss" stripe style="width: 100%" max-height="400"> 
							<el-table-column prop="fund_id" label="序号" width="100">
							</el-table-column>
							<el-table-column prop="company" label="企业名称">
							</el-table-column>
  						</el-table>
					</div>
				</div>
				<div class="difference">
					<div class="difference_title flex_between">
						<div class="difference_title_left">
							<!-- 4、导入表中共有{{ newly.length }}个企业名称与子基金全称的对应关系与系统中的不一致;有{{ newly.length }}个企业的项目名称与系统中的不一致 -->
							4、系统中和导入表中企业名称与子基金全称对应关系不一致，共：{{newly.length}}条
						</div>
						<div class="cursor" @click="changeDifferenceStatus(4)">
							<span v-if="!currentIndex4" class="iconfont icon-webicon212"></span>
							<span v-else class="iconfont icon-xiangshang"></span>
						</div>
					</div>
					<div class="difference_main table" v-if="currentIndex4&&newly.length">
						<el-table :data="newly" stripe style="width: 100%" max-height="400"> 
							<el-table-column prop="fund_id" label="序号" width="100">
    							</el-table-column>
    							<el-table-column prop="company" label="企业名称">
    							</el-table-column>
    							<el-table-column prop="sys_child_fund_name" label="系统中子基金全称">
    							</el-table-column>
    							<el-table-column prop="child_fund_name" label="导入表中子基金全称">
    							</el-table-column>
  						</el-table>
					</div>
				</div>
				<div class="difference">
					<div class="difference_title flex_between">
						<div class="difference_title_left">
							<!-- 5、导入表无企业的项目名称与系统中不一致 -->
							<!-- 5、项目名称与系统中不一致，共：{{}}条 -->
							5、项目名称与系统中不一致
						</div>
						<div class="cursor" @click="changeDifferenceStatus(5)">
							<span v-if="!currentIndex5" class="iconfont icon-webicon212"></span>
							<span v-else class="iconfont icon-xiangshang"></span>
						</div>
					</div>
					<div class="difference_main table" v-if="currentIndex5&&project_differ.length">
						<el-table :data="project_differ" stripe style="width: 100%" max-height="400"> 
							<el-table-column prop="fund_id" label="序号" width="100">
    							</el-table-column>
    							<el-table-column prop="project" label="项目名称">
    							</el-table-column>
    							<el-table-column prop="company" label="企业名称">
    							</el-table-column>
    							<el-table-column prop="child_fund_name" label="子基金全称">
    							</el-table-column>
  						</el-table>
					</div>
				</div>
				<div class="difference">
					<div class="difference_title flex_between">
						<div class="difference_title_left">
							<!-- 6、系统中已有{{ update_recode }}条数据已更新 -->
							6、系统中数据更新，共：{{update_recode}}条
						</div>
					</div>
				</div>
			</div>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog_btn dialog_btn_cancel" @click="is_dialog_show=false">关闭</button>
				<button class="dialog_btn dialog_btn_submit" @click="downloadErrorInfo">下载详情</button>
			</div>
		</el-dialog>
		<!--导入信息差异弹窗结束-->
		
	</div>
</template>

<script>
	import { investListReportSubApi,investDownloadTempletReportSubApi,investDownloadInfoReportSubApi,investDownloadErrorInfoReportSubApi,investUploadInfoReportSubApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	
	export default {
		name: 'subFundInvestTotal',
		data() {
			return {
				fund_name: '',               
				project_name: '',
				total: 0,
				currentPage: 1,
				pageSize: 10,
				tableData: [ ],
				
				is_dialog_show: false,
				currentIndex1: true,
				currentIndex2: true,
				currentIndex3: true,
				currentIndex4: true,
				currentIndex5: true,
				fund_not_exits: [ ],
				project_not_exits: [ ],
				miss: [ ],
				newly: [ ],
				project_differ: [ ],
				update_recode: 0,
			}
		},
		created() {
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(is_init_page){
				if (is_init_page) {
					this.currentPage = 1;
				}
				investListReportSubApi({
					chidfund: this.fund_name,
					project: this.project_name,
					page: this.currentPage,
					pageSize: this.pageSize
				}).then(res => {
					//console.log(res);
					if (res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.total;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadTemplet(){
				investDownloadTempletReportSubApi({
					
				}).then(res => {
					//console.log(res);
					if (res.data.msgNo == 1) {
						window.location.href = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadInfo(){
				investDownloadInfoReportSubApi({
					
				}).then(res => {
					//console.log(res);
					if (res.data.msgNo == 1) {
						window.location.href = res.data.payload.path;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadErrorInfo(){
				var data = {
					fund_not_exits: this.fund_not_exits,
					project_not_exits: this.project_not_exits,
					miss: this.miss,
					newly: this.newly,
					project_differ: this.project_differ
				}
				console.log(data);
				investDownloadErrorInfoReportSubApi({
					data: data
				}).then(res => {
					if (res.data.msgNo == 1) {
						window.location.href = res.data.payload.path;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			uploadInfo(){
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
					dialogConfirm('info', '请选择Excel格式文件')
					inputDOM.value = '';
					return false
				}
				investUploadInfoReportSubApi({},file).then(res => {
					if(res.data.msgNo == 1) {
						//console.log(res);
						this.fund_not_exits = res.data.payload.fund_not_exits;
						this.project_not_exits = res.data.payload.project_not_exits;
						this.miss = res.data.payload.miss;
						this.newly = res.data.payload.newly;
						this.project_differ = res.data.payload.project_differ;
						this.update_recode = res.data.payload.update_recode;
						this.is_dialog_show = true;
						this.getInitData(1)
					} else {
						dialogConfirm('info', '导入失败，请核对表格数据');
					}
					inputDOM.value = '';
				})
			},
			
			
			changeDifferenceStatus(e){
				if (e == 1) {
					this.currentIndex1 = !this.currentIndex1;
				} else if (e == 2){
					this.currentIndex2 = !this.currentIndex2;
				} else if (e == 3){
					this.currentIndex3 = !this.currentIndex3;
				} else if (e == 4){
					this.currentIndex4 = !this.currentIndex4;
				} else if (e == 5){
					this.currentIndex5 = !this.currentIndex5;
				} else {
					//console.log('英雄联盟');
				}

			},
			
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
		},
	}
</script>

<style scoped="scoped">
	.body{
		padding: 0 !important;
	}
	.search_group{
		padding: 30px 0;
    		border-bottom: 1px solid #F1F1F1;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.date_box{
		width: 162px;
	}
	.date_middle_text{
		margin: 0 10px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.table_title{
		margin: 26px 0;
		color: #6292F4;
		font-size: 14px;
	}
	.table_title span{
		color: #6292F4;
	}
	.table_title button{
		margin-left: 10px;
	}
	
	/*差异对照弹出框开始*/
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	
	.difference{
		margin-bottom: 30px;
	}
	.difference_title_left{
		color: #6292F4;
	}
	.dialog_body{
		padding: 0 30px;
	}
	.difference_main{
		margin-top: 20px;
	}
	/*差异对照弹出框结束*/
</style>


